// login register
.loginmain {
  background: #f1f2f7;
}
.site-link {
  padding: 5px 15px;
  position: fixed;
  z-index: 99999;
  background: #fff;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.14), 0 4px 8px rgba(0, 0, 0, 0.28);
  right: 30px;
  bottom: 30px;
  border-radius: 10px;
  img {
    width: 30px;
    height: 30px;
  }
}
.loginCard {
  width: 65%;
  margin: 10% auto;
  background-image: url("../../../assets/images/bg-01.jpg");
  min-height: 400px;
  display: block;
  position: relative;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.14), 0 4px 8px rgba(0, 0, 0, 0.28);
  > div {
    vertical-align: middle;
    text-align: center;
    color: #fff;
    width: 50%;
    float: left;
  }
  .login-btn {
    position: absolute;
    top: 40%;
  }
  .rgstr-btn {
    right: 0;
    position: absolute;
    top: 40%;
  }
  button {
    background: transparent;
    display: inline-block;
    padding: 10px 30px;
    border: 3px solid #fff;
    border-radius: 50px;
    background-clip: padding-box;
    position: relative;
    color: #fff;
    transition: all 0.25s;
    &.dark {
      border-color: #bd59d4;
      background: #bd59d4;
      box-shadow: 0 10px 30px 0px rgba(189, 89, 212, 0.5);
      -moz-box-shadow: 0 10px 30px 0px rgba(189, 89, 212, 0.5);
      -webkit-box-shadow: 0 10px 30px 0px rgba(189, 89, 212, 0.5);
      -o-box-shadow: 0 10px 30px 0px rgba(189, 89, 212, 0.5);
      -ms-box-shadow: 0 10px 30px 0px rgba(189, 89, 212, 0.5);
    }
    &:active {
      box-shadow: none;
    }
    &:focus {
      outline: none;
    }
  }
  .wrapper {
    > form {
      padding: 15px 30px 30px;
      transition: all 0.5s;
      background: #fff;
      width: 100%;
      border-radius: 20px;
      &:focus {
        outline: none;
      }
      > div {
        position: relative;
        margin-bottom: 15px;
      }
    }
    #login {
      padding-top: 20%;
      visibility: visible;
    }
    #register {
      transform: translateY(-80%) translateX(100%);
      visibility: hidden;
    }
    &.move {
      #register {
        transform: translateY(-100%) translateX(0%);
        visibility: visible;
      }
      #login {
        transform: translateX(-100%);
        visibility: hidden;
      }
    }
    label {
      position: absolute;
      top: -7px;
      font-size: 12px;
      white-space: nowrap;
      background: #fff;
      text-align: left;
      left: 15px;
      padding: 0 5px;
      color: #999;
      pointer-events: none;
    }
    h3 {
      margin-bottom: 25px;
    }
    input {
      height: 40px;
      width: 100%;
      border: solid 1px #999;
      border-radius: 20px;
      padding-left: 10px;
      &:focus {
        outline: none;
        border-color: #ff4931;
        box-shadow: none !important;
        border-bottom: 1px solid #ff4931 !important;
      }
    }
  }
  .move button.dark {
    border-color: #bd59d4;
    background: #bd59d4;
    box-shadow: 0 10px 30px 0px rgba(189, 89, 212, 0.5);
    -moz-box-shadow: 0 10px 30px 0px rgba(189, 89, 212, 0.5);
    -webkit-box-shadow: 0 10px 30px 0px rgba(189, 89, 212, 0.5);
    -o-box-shadow: 0 10px 30px 0px rgba(189, 89, 212, 0.5);
    -ms-box-shadow: 0 10px 30px 0px rgba(189, 89, 212, 0.5);
  }
  .splits p {
    font-size: 18px;
  }
  > .wrapper {
    position: absolute;
    width: 40%;
    height: 120%;
    top: -10%;
    left: 5%;
    border-radius: 20px;
    background: #fff;
    box-shadow: 0 0 4px rgba(0, 0, 0, 0.14), 0 4px 8px rgba(0, 0, 0, 0.28);
    transition: all 0.5s;
    color: #303030;
    overflow: hidden;
    &.move {
      left: 45%;
    }
  }
}
@media (max-width: 767px) {
  .loginCard {
    padding: 5px;
    margin: 0;
    width: 100%;
    display: block;
    > div {
      display: inline-block;
    }
    > .wrapper {
      position: relative;
      height: auto;
      top: 0;
      left: 0;
      width: 100%;
      &.move {
        left: 0;
      }
    }
  }
  .splits {
    width: 50%;
    background: #fff;
    float: left;
    button {
      width: 100%;
      border-radius: 0;
      background: #505050;
      border: 0;
      opacity: 0.7;
      &.active {
        opacity: 1;
        background: #ff4931;
      }
    }
    p {
      display: none;
    }
    &.rgstr-btn button.active {
      background: #e0b722;
    }
  }
}
